body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.left-bar {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 2px;
}

.heading {
  color: white;
  font-size: 5em;
  letter-spacing: 5px;
  margin-bottom: 0;
  margin-top: 0;
  padding-top: 30px;
}

@media (max-width: 700px) {
  .heading {
    color: white;
    font-size: 3em;
    letter-spacing: 0;
  }

  .left-bar {
    opacity: 0;
  }
}

.smallscreen-heading {
  color: white;
  font-size: 2.7em;
  letter-spacing: 0;
  margin-top: 0;
  margin-bottom: 10px;
  padding-top: 30px;
}

.subheading {
  color: white;
  font-size: 1em;
}

.smallscreen-subheading {
  color: white;
  font-size: 1.2em;
  margin: 0 auto;
  text-align: center;
  width: 80%;
}

.smallscreen-subheading a {
  color: #61dafb;
}

.subheading a {
  color: #61dafb;
}

.about {
    background-color: #28334A;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: Helvetica;
    font-weight: 300;
    padding-bottom: 50px;
}

#aboutMeImg {
    border-radius: 50%;
    width: 215px;
    margin: 30px;
    border: 1px #ccc solid;
}

#about-me-text {
    width: 40%;
    margin: 0 auto;
    text-align: center;
    line-height: 150%;
}

@media (max-width: 700px) {
    #about-me-text {
        width: 85%;
    }
