.navbar {
    display: flex;
    align-items: center;
    background: #212a3b;
    color: white;
    font-family: Helvetica;
    font-weight: 300;
    width: 100%;
    position: fixed;
    transition: 0.5s;
    z-index: 1;
}

.fixed {
    background-color: black;
}

.navbar__title {
    margin-right: auto;
    font-size: 150%;
    vertical-align: middle;
    padding: 16px 16px;
}

.navbar__item {
    padding: 16px 16px;
    cursor: pointer;
    vertical-align: middle;
    transition: 0.5s;
}

.navbar__item:hover {
    color: #61dafb;
}

.mobile-navbar {
    display: flex;
    align-items: center;
    background-color: #212a3b;
    color: white;
    font-family: Helvetica;
    font-weight: 300;
    width: 100%;
    position: fixed;
    transition: 0.5s;
    z-index: 1;
}

.mobile-title {
    color: white;
    margin-left: auto;
    font-size: 150%;
    vertical-align: middle;
    padding: 16px 16px;
}

#hamburger {
    color: white;
    background: none;
    outline: none;
    text-decoration: none;
    border: none;
    padding: 16px;
    font-size: 170%;
    transition: 0.5s;
    z-index: 1;
    transition: all 0.3s linear;
}

